<template>
  <div id="assets">
  	<el-dialog
      title="Assets"
      :visible.sync="assetsDialogVisible"
      :fullscreen="true">
      <div class="columns is-multiline">
        <div class="column is-8">
          <div class="columns is-multiline">
            <div class="column is-narrow" v-for="asset,k in Assets">
              <div class="box assetBox">
                <el-button type="primary" icon="ion-md-copy" @click="copyUrl(asset.url)"></el-button>
                <el-button type="danger" icon="ion-md-trash" @click="deleteAsset(asset.key)"></el-button>
                <p @click="viewAsset(asset)">{{asset.asset}}</p>
              </div>
            </div>
          </div>
        </div>
        <div class="column is-4">
          <el-upload
            class="upload-demo"
            drag
            :action="assetUploadAction"
            ref="assetUpload"
            :file-list="fileList"
            list-type="picture"
            :on-success="assetsUploaded"
            :on-progress="assetsUploadProgress"
            multiple>
            <!-- :auto-upload="false"> -->
            <i class="el-icon-upload"></i>
            <div class="el-upload__text">Drop file here or <em>click to upload</em></div>
            <div class="el-upload__tip" slot="tip">jpg/png/gif/mp4/webm/css/js/json files with a size less than 5mb</div>
          </el-upload>  
          <el-progress v-if="uploadPercentage != 0" :text-inside="true" :stroke-width="18" :percentage="uploadPercentage" status="success"></el-progress>
        </div>
      </div>
    </el-dialog>

    <el-dialog v-if="previewAsset"
      :title="previewAsset.asset"
      :visible.sync="showPreviewDialog"
      width="50%"
      center
      custom-class="previewDialog">
      <iframe class="embed" v-if="previewAsset" :src="previewAsset.url"></iframe>
      <span slot="footer" class="dialog-footer">
        <el-button @click="previewAsset=null; showPreviewDialog = false">Cancel</el-button>
      </span>
    </el-dialog>

  </div>
</template>

<script>
export default {
  name: 'Assets',
  data () {
    return {
      assetsDialogVisible : false,
      showPreviewDialog: false,
      Assets: null,
      previewAsset: null,
      fileList: [],
      uploadPercentage: 0,
      assetUploadAction: null,
    }
  },
  created: function(){
    this.assetUploadAction = "/api/assets/save"
    var self = this
    this.$bus.$on('showAssets', (x) => {
      self.assetsDialogVisible = !self.assetsDialogVisible
      this.getAssets()
    })
  },
  mounted: function(){
    this.getAssets()
  },
  methods: {
    getAssets: function(){
      var self = this
      this.axios.post('/api/assets', {
        })
        .then(response => {
          self.Assets = response.data
        })
        .catch(e => {
        })
    },
    deleteAsset: function(key){
      var self = this
      this.axios.post('/api/assets/delete', {
          key: key,
        })
        .then(response => {
          self.Assets = response.data
        })
        .catch(e => {
        })
    },
    copyUrl: function(x){
      var self = this
      this.$copyText(x).then(function (e) {
        self.$message({
          message: 'Url Copied : '+x,
          type: 'success',
          showClose: true,
        });
      }, function (e) {
        self.$message({
          message: "Can't copy URL : "+x,
          type: 'warning',
          showClose: true,
        });
      })
    },
    assetsUploaded: function(r, f, fl){
      if(r.status || r.status == false){
        this.$message({
          showClose: true,
          message: r.error,
          type: 'error'
        });
      }else{
        this.$message({
          message: 'Asset successfully uploaded',
          type: 'success',
          showClose: true,
        });
        this.Assets = r
      }
      this.uploadPercentage=0
    },
    assetsUploadProgress: function(e, f, fl){
      this.uploadPercentage = e.percent
    },
    viewAsset: function(p){
      this.previewAsset = p
      this.showPreviewDialog = true
    }
  }
}
</script>
<style>
.el-dialog.is-fullscreen{
  background: #4a4d4e;
  color: #fff;
}
.el-dialog__close.el-icon.el-icon-close{
  color: white;
}
.el-upload__tip{
  color: #fff;
}
.el-dialog__header, .el-dialog__title{
  color: #fff;
}
.assetBox{
  cursor: pointer;
  padding: 0.5rem;
}
.assetBox:hover{
  transition: all 0.3s cubic-bezier(.25,.8,.25,1);
  box-shadow: 0 3px 6px rgba(0,0,0,0.16), 0 3px 6px rgba(0,0,0,0.23);
}
.el-button.is-circle{
  padding: 12px !important;
  float: left;
  margin-bottom: 10px;
  margin-right: 0px;
}
.assetBox p{
  clear: both;
  margin-top: 10px;
}
.previewDialog .el-dialog__title{
  color: teal;
}
.embed{
  width: auto;
  height: 50vh;
  display: block;
  margin: 0 auto;
  max-width: 100%;
  min-width: 100%;
}
</style>



